.main{position:relative;z-index:2;border-top:1px solid #e6e6e6}label[for='toggle-all']{display:none}.toggle-all{position:absolute;top:-55px;left:-12px;width:60px;height:34px;text-align:center;border:none}@media screen and (-webkit-min-device-pixel-ratio: 0){.toggle-all{background:none;-webkit-transform:rotate(90deg);transform:rotate(90deg);-webkit-appearance:none;-moz-appearance:none;appearance:none}}.toggle-all:before{content:'❯';font-size:22px;color:#e6e6e6;padding:10px 27px 10px 27px}.toggle-all:checked:before{color:#737373}.todo-list{margin:0;padding:0;list-style:none}.todo-list li{position:relative;font-size:24px;border-bottom:1px solid #e6e6e6}.todo-list li:last-child{border-bottom:none}.todo-list li .destroy,.todo-list li .edit-btn{display:none;position:absolute;top:0;right:10px;bottom:0;width:23px;height:23px;line-height:23px;margin:auto 0;font-size:35px;color:#cc9a9a;margin-bottom:16px;-webkit-transition:color 0.2s ease-out;transition:color 0.2s ease-out}.todo-list li .destroy:hover,.todo-list li .edit-btn:hover{color:#af5b5e}.todo-list li .destroy:after,.todo-list li .edit-btn:after{content:'×'}.todo-list li .edit-btn{right:38px;background:url(data:image/png;base64,);background-size:contain;opacity:.65;-webkit-transition:opacity 0.2s ease-out;transition:opacity 0.2s ease-out}.todo-list li .edit-btn:hover{opacity:1}.todo-list li .edit-btn:after{content:''}.todo-list li .toggle{text-align:center;width:40px;height:auto;position:absolute;top:0;bottom:0;margin:auto 0;border:none;-webkit-appearance:none;-moz-appearance:none;appearance:none}@media screen and (-webkit-min-device-pixel-ratio: 0){.todo-list li .toggle{background:none;height:40px}}.todo-list li .toggle:after{content:url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#ededed" stroke-width="3"/></svg>')}.todo-list li .toggle:checked:after{content:url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#bddad5" stroke-width="3"/><path fill="#5dc2af" d="M72 25L42 71 27 56l-4 4 20 20 34-52z"/></svg>')}.todo-list li label{white-space:pre-line;word-break:break-all;padding:15px 60px 15px 15px;margin-left:45px;display:block;line-height:1.2;-webkit-transition:color 0.4s;transition:color 0.4s}.todo-list li:hover .destroy,.todo-list li:hover .edit-btn{display:block}.todo-list li .edit{display:none}.todo-list li.editing{border-bottom:none;padding:0}.todo-list li.editing:last-child{margin-bottom:-1px}.todo-list li.editing .edit{display:block;width:506px;padding:13px 17px 12px 17px;margin:0 0 0 43px}.todo-list li.editing .view{display:none}.todo-list li.completed label{color:#d9d9d9;text-decoration:line-through}



